.min-nav {
  padding-bottom: 30px;
  @media (max-width: 480px) {
    padding-bottom: 0;
  }

  .top-bar {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    padding: 30px 0;
    @media (max-width: 991px) {
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      flex-direction: row;
    }

    & > ul {
      width: calc(100% / 4);
      @media (max-width: 991px) {
        width: calc(100% / 1);
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;
      }

      display: flex;
      flex-direction: row-reverse;
      align-items: center;
      li {
        &.heart,
        &.cart,
        &.user-box {
          // width: 10%;
          &:not(:first-of-type) {
            margin-right: 10px;
            body.ar & {
              margin-right: unset;
              margin-left: 10px;
            }
          }
          cursor: pointer;
        }
        &.user-box {
          position: relative;
          ul {
            background-color: #fff;
            box-shadow: 1px 1px 12px rgba(#000, 0.16);
            position: absolute;
            width: 120px;
            padding: 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            left: 50%;
            top: 110%;
            transform: translate(-50%, 50%);
            opacity: 0;
            visibility: hidden;
            transition: 0.5s;
            z-index: 12;
            li {
              backface-visibility: hidden;

              a {
                color: #000;
                transition: 0.5s;
                &:hover {
                  color: $color-main;
                }
              }
            }
          }
          &:hover {
            ul {
              transform: translate(-50%, 0);
              opacity: 1;
              visibility: visible;
            }
          }
        }
        &.lang {
          margin-right: 15px;
          body.ar & {
            margin-right: unset;
            margin-left: 15px;
          }
          text-transform: uppercase;
          transition: 0.5s;
          cursor: pointer;
          font-family: "Roboto-Medium";
          &:hover {
            color: $color-main;
          }
          i {
            margin-left: 4px;
            body.ar & {
              margin-left: unset;
              margin-right: 4px;
            }
          }
          position: relative;
          ul {
            background-color: #fff;
            box-shadow: 1px 1px 12px rgba(#000, 0.16);
            position: absolute;
            width: 120px;
            padding: 10px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            left: 50%;
            top: 110%;
            transform: translate(-50%, 50%);
            opacity: 0;
            visibility: hidden;
            transition: 0.5s;
            z-index: 12;
            li {
              backface-visibility: hidden;
              a {
                color: #000;
                font-family: "Roboto-Medium";
                transition: 0.5s;
                &:hover {
                  color: $color-main;
                }
              }
            }
          }
          &:hover {
            ul {
              transform: translate(-50%, 0);
              opacity: 1;
              visibility: visible;
            }
          }
        }
        a {
          transition: 0.5s;
          position: relative;
          color: #fff;
        }
        svg {
          transition: 0.5s;
        }

        &.cart {
          a {
            span {
              transition: 0.5s;
              position: absolute;
              bottom: 24px;
              right: -1px;
              width: 15px;
              height: 15px;
              font-size: 13px;
              background-color: $color-main;
              border-radius: 100px;
              display: flex;
              justify-content: center;
              align-items: center;
            }
          }
        }
        &:hover {
          svg {
            fill: $color-main !important;
            .cls-1 {
              stroke: $color-main !important;
            }
          }
          & > a {
            span {
              background-color: #000;
              box-shadow: 0 0 12px rgba(#000, 0.1);
            }
          }
        }
      }
    }
    .search-container {
      width: calc(100% / 2);
      @media (max-width: 991px) {
        width: calc(100% / 1);
        justify-content: center;
        align-items: center;
      }

      display: flex;
      justify-content: center;
      align-items: center;

      .search-box {
        width: 100%;
        display: flex;
        flex-direction: row-reverse;
        align-items: stretch;
        position: relative;
        input {
          width: 100%;
          padding: 10px 5px;
          border: 1px solid #a4b3b6;
          &::placeholder {
            color: #a4b3b6;
            font-size: 17px;
          }
        }
        button {
          padding: 5px 12px;
          border: unset;
          background-color: $color-main;
          position: absolute;
          height: 100%;
          transition: 0.5s;
          &:hover {
            background-color: $color-dark2;
          }
          i {
            font-size: 25px;
            color: #fff;
            border-radius: 0;
          }
        }
      }
    }
    .logo-container {
      width: calc(100% / 4);
      @media (max-width: 991px) {
        width: calc(100% / 1);
        justify-content: center;
        align-items: center;
        margin-bottom: 20px;
      }

      justify-content: flex-start;
      align-items: center;
      display: flex;
    }
  }
  .menu {
    background-color: $color-dark;
    padding: 20px 0;
    ul {
      display: flex;
      justify-content: space-between;
      a {
        color: transparent;
        transition: 0.5s;
        text-transform: uppercase;
        background-clip: text;
        display: inline-block;
        background-image: linear-gradient(
          to right,
          #d93f87,
          #d93f87 50%,
          #fff 50%,
          #fff
        );
        background-size: 200% 100%;
        background-position: 100%;
        transition: all 0.5s cubic-bezier(0, 0, 0.23, 1);

        &:hover {
          background-position: 0%;
        }
      }
    }
  }
}

.nav-trigger {
  background: $color-main;

  position: fixed;
  z-index: 200;
  right: 7.7%;

  @media (max-width: 480px) {
    right: 1.7%;
  }
  body.ar & {
    right: unset;

    left: 7.7%;

    @media (max-width: 480px) {
      left: 1.7%;
    }
  }

  top: 20px;
  width: 55px;
  height: 55px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-radius: 50%;
  transition: 0.5s;
  cursor: pointer;
  span {
    width: 28px;
    display: block;
    height: 3px;
    background-color: #fff;
    border-radius: 120px;
    transition: 0.5s;
    &:not(:last-of-type) {
      margin-bottom: 5px;
    }
  }
  &.active {
    background-color: #fff;
    span {
      background-color: $color-main;

      &:nth-child(1) {
        transform: translateY(8px) rotate(135deg);
      }
      &:nth-child(2) {
        transform: rotate(135deg);
        opacity: 0;
      }
      &:nth-child(3) {
        transform: translateY(-8px) rotate(-135deg);
      }
    }
  }
}

.sm-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  opacity: 0;
  overflow: hidden;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.38s;
  // background-color: rgba(#eff3f5 ,.5 );
  z-index: 100;
  &::before {
    content: "";
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: -15px;
    background: $color-main;
    transform-origin: 0 0;
    transform: skew(-14deg) translateX(-120%);
    transition: all 0.275s 0.1s;
    z-index: 1;
  }
  ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 20;
    margin-bottom: 0;
    padding-inline-start: 0;
    li {
      &:not(:last-of-type) {
        margin-bottom: 22px;
      }
      a {
        font-size: 20px;
        color: #fff;
        opacity: 1;
        text-decoration: none;
      }
      opacity: 0;
      transform: translateX(50px);
      transition: all 0.5s;
    }
  }
  &.active {
    opacity: 1;
    visibility: visible;
    &::before {
      transform: skew(0) translateX(0);
    }
    ul li {
      opacity: 1;
      &:nth-of-type(1) {
        transition: all 275ms 175ms;
        transform: translateX(0);
      }
      &:nth-of-type(2) {
        transition: all 275ms 225ms;
        transform: translateX(0);
      }
      &:nth-of-type(3) {
        transition: all 275ms 275ms;
        transform: translateX(0);
      }
      &:nth-of-type(4) {
        transition: all 275ms 325ms;
        transform: translateX(0);
      }
      &:nth-of-type(5) {
        transition: all 275ms 380ms;
        transform: translateX(0);
      }
      &:nth-of-type(6) {
        transition: all 275ms 425ms;
        transform: translateX(0);
      }
      &:nth-of-type(7) {
        transition: all 275ms 475ms;
        transform: translateX(0);
      }
      &:nth-of-type(8) {
        transition: all 275ms 525ms;
        transform: translateX(0);
      }
    }
  }
}

