footer {
  padding: 70px 0 0;
  padding-bottom: 0;
  overflow: hidden;
}

@media (max-width: 991px) {
  footer {
    padding: 40px 0 0;
  }
}

@media (max-width: 991px) {
  footer .foot-links {
    margin-bottom: 15px;
  }
}

footer .foot-links .collapse {
  display: block;
}

@media (max-width: 991px) {
  footer .foot-links .collapse {
    display: none;
  }
}

footer .foot-links .collapse.in {
  display: block;
}

footer .foot-links > button {
  font-size: 16px;
  font-family: "Roboto-Medium";
  color: #000;
  margin-bottom: 15px;
  background-color: transparent;
  border: 0;
  padding: 0;
  pointer-events: none;
}

footer .foot-links > button.trans:after {
  content: "\2212";
}

@media (max-width: 991px) {
  footer .foot-links > button {
    width: 100%;
    margin: auto;
    pointer-events: all;
    margin-bottom: 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
  }
}

@media (max-width: 480px) {
  footer .foot-links > button {
    width: 100%;
  }
}

footer .foot-links > button:after {
  content: "\002B";
  margin-left: 5px;
  font-size: 18px;
  float: left;
  display: none;
}

@media (max-width: 991px) {
  footer .foot-links > button:after {
    display: block;
  }
}

@media (max-width: 991px) {
  footer .foot-links ul {
    padding-top: 10px;
    padding-right: 10px;
  }
}

footer .foot-links ul li:not(:last-child) {
  margin-bottom: 10px;
}

footer .foot-links a {
  font-size: 14px;
  font-family: "Roboto-Regular";
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  color: #000;
  position: relative;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: -webkit-gradient(
    linear,
    left top,
    right top,
    from(#d93f87),
    color-stop(50%, #d93f87),
    color-stop(50%, #000)
  );
  background-image: linear-gradient(to right, #d93f87, #d93f87 50%, #000 50%);
  background-size: 200% 100%;
  background-position: 100%;
  display: inline-block;
  transition: all 0.5s cubic-bezier(0, 0, 0.23, 1);
}

footer .foot-links a:hover {
  background-position: 0%;
}

footer .foot-links a:hover {
  color: #d93f87;
}

@media (min-width: 992px) {
  footer .newsletter {
    margin-top: 35px;
    text-align: center;
  }
}

@media (min-width: 1200px) {
  footer .newsletter {
    margin-top: 0;
    text-align: right;
  }
  body.en footer .newsletter {
    text-align: left;
  }
}

@media (max-width: 991px) {
  footer .newsletter {
    margin-top: 35px;
    text-align: center;
  }
}

footer .newsletter > p {
  font-size: 16px;
  font-family: "Roboto-Medium";
  color: #000;
  margin-bottom: 20px;
}

footer .newsletter > span {
  font-size: 14px;
  font-family: "Roboto-Regular";
  color: #000;
  margin-bottom: 15px;
  display: block;
}

footer .frm {
  position: relative;
  margin-bottom: 17px;
  width: 360px;
}

@media (min-width: 992px) {
  footer .frm {
    margin: auto;
    margin-bottom: 17px;
  }
}

@media (max-width: 991px) {
  footer .frm {
    margin: auto;
    margin-bottom: 17px;
  }
}

@media (max-width: 375px) {
  footer .frm {
    width: 280px;
  }
}

footer .frm input {
  padding: 0 20px;
  height: 40px;
  width: 100%;
  @media (max-width: 1024px) and (min-width: 769px) {
    width: 80%;
  }
  border: 0;
  color: #afafaf;
  font-size: 14px;
  background-color: #fff;
  border: 1px solid #707070;
  border-radius: 200px;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

footer .frm input::-webkit-input-placeholder {
  color: #afafaf;
}

footer .frm input::-moz-placeholder {
  color: #afafaf;
}

footer .frm input:-ms-input-placeholder {
  color: #afafaf;
}

footer .frm input::-ms-input-placeholder {
  color: #afafaf;
}

footer .frm input::placeholder {
  color: #afafaf;
}

footer .frm button {
  position: absolute;
  top: 50%;
  left: 6px;
  @media (max-width: 1024px) and (min-width: 769px) {
    left: 44px;
  }

  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: $color-main;
  border: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

body.en footer .frm button {
  left: unset;
  right: 6px;
  @media (max-width: 1024px) and (min-width: 769px) {
    right: 44px;
  }
}

footer .frm button:hover {
  background-color: $color-dark2;
}

footer .frm button i {
  font-size: 12px;
  color: #fff;
}

.end {
  margin-top: 80px;
}

@media (max-width: 991px) {
  .end {
    margin-top: 40px;
  }
}

@media (max-width: 767px) {
  .end {
    margin-top: 10px;
  }
}

.end .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

@media (max-width: 600px) {
  .end .content {
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
    -ms-flex-direction: column-reverse;
    flex-direction: column-reverse;
    padding: 0 0 10px;
  }
}

@media (max-width: 767px) {
  .end .content {
    text-align: center;
  }
}

.end .content p,
.end .content a {
  font-size: 13px;
  color: #707070;
  margin-bottom: 0;
}

@media (max-width: 767px) {
  .end .content p,
  .end .content a {
    font-size: 13px;
  }
}

@media (max-width: 600px) {
  .end .content p,
  .end .content a {
    margin-top: 10px;
  }
}

@media (max-width: 480px) {
  .end .content p {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    margin-bottom: 0;
  }
}

.end .content a img {
  margin-right: 5px;
}

body.en .end .content a img {
  margin-right: 0;
  margin-left: 5px;
}

@media (max-width: 480px) {
  .end .content a {
    margin-top: 10px;
  }
}
.social {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
  a {
    color: $color-dark;
    font-size: 20px;
    margin: 0 10px;
    transition: 0.5s;
    &:hover {
      color: $color-main;
    }
  }
}
