﻿html {
  overflow-y: hidden;
}
body {
  font-family: "Roboto-Medium";
  direction: ltr;
  overflow-x: hidden;
  &.overflow {
    overflow-y: hidden;
  }
  &::-webkit-scrollbar {
    background-color: #eff3f5;
    width: 7px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $color-main;
  }
}
.container {
  position: relative;
}
a {
  &:hover {
    color: currentColor;
  }
}
input,
a,
button {
  font-family: "Roboto-Regular";

  &:focus {
    outline: none;
    text-decoration: none;
  }

  &:hover {
    text-decoration: none;
  }
}

button {
  cursor: pointer;

  &:focus,
  &:active {
    outline: none;
    box-shadow: none;
  }
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
