.home-header {
  .banners-conatiner {
    .banners-right {
      width: calc(100% / 1.1);
      @media (max-width: 1024px) {
        width: 100%;
      }

      .top-sm-banners {
        display: flex;
        justify-content: space-between;
        @media (max-width: 480px) {
          flex-wrap: wrap;
        }

        .sm-banner {
          padding-bottom: 20px;
          @media (max-width: 480px) {
            width: calc(100% / 2);
          }

          &:not(:first-of-type) {
            padding-left: 20px;
            body.ar & {
              padding-left: unset;
              padding-right: 20px;
            }
          }
          @media (max-width: 480px) {
            padding: 5px;
            &:not(:first-of-type) {
              padding-left: 5px;
              body.ar & {
                padding-left: unset;
                padding-right: 5px;
              }
            }
          }
        }
      }
      .bottom-banners {
        overflow: hidden;
        display: flex;
        position: relative;
        @media (max-width: 480px) {
          height: 70vh;
        }

        .overlay {
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          background-color: rgba(#000, 0.5);
          width: 100%;
          height: 100%;
          // text-align: center;
          display: flex;
          justify-content: center;
          padding: 40px;
          flex-direction: column;
          @media (max-width: 480px) {
            justify-content: center;
            align-items: center;
            padding: 10px;
          }

          h1 {
            color: #fff;
            font-size: 40px;
            line-height: 1.3;
            margin-bottom: 40px;
            @media (max-width: 480px) {
              font-size: 20px;
              text-align: center;
            }

            span {
              font-family: "segoeuisl";
            }
          }
        }
        img {
          object-fit: cover;
          width: 100%;
          transform: scale(1.03);
          transition: 0.5s;
        }
        &:hover {
          img {
            transform: scale(1);
          }
        }
      }
    }
    .banners-left {
      @media (max-width: 1024px) {
        display: none;
      }
      padding: 0 20px;
      display: flex;
      overflow: hidden;
      img {
        transition: 0.5s;
        transform: scale(1, 1.03);
      }
      &:hover {
        img {
          transform: scale(1);
        }
      }
    }
  }
}
.product-section {
  padding: 60px 0;
  @media (max-width: 768px) {
    padding: 40px 0;
  }

  .min-button {
    margin: 30px auto 0;
  }
}
.banner-section {
  padding: 60px 0;
  .image-box {
    overflow: hidden;
    .overlay {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      padding: 60px;
      flex-direction: column;
      @media (max-width: 425px) {
        text-align: center;
        justify-content: center;
        align-items: center;
      }

      h2 {
        color: #000;
        font-size: 40px;
        line-height: 1.3;
        margin-bottom: 40px;
        @media (max-width: 768px) {
          font-size: 30px;
        }
        @media (max-width: 425px) {
          font-size: 23px;
        }

        span {
          font-family: "segoeuisl";
        }
      }
    }
    img {
      transition: 0.5s;
      transform: scale(1.03);
    }
    &:hover {
      img {
        transform: scale(1);
      }
    }
  }
}
.services-section {
  padding: 30px 0;
  background-color: $color-dark;
  .services-container {
    display: flex;
    flex-wrap: wrap;
    .services-box {
      width: calc(100% / 4);
      @media (max-width: 1024px) {
        width: calc(100% / 2);
        margin-bottom: 20px;
      }
      @media (max-width: 425px) {
        width: calc(100% / 1);
      }

      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      img {
        height: 60%;
        display: flex;
        margin-bottom: 25px;
      }
      h3 {
        color: #fff;
        font-size: 14px;
        font-family: "Roboto-Regular";
        margin: 0;
      }
    }
  }
}
