.min-header {
  width: 100%;
  text-align: center;
  justify-content: center;
  align-items: center;
  display: flex;
  margin-bottom: 20px;
  text-transform: uppercase;
  font-family: "Roboto-Regular";
  font-size: 40px;
}
.product-conatiner {
  display: flex;
  flex-wrap: wrap;

  @media (max-width: 1024px) {
    justify-content: center;
    align-items: center;
  }

  .product-box {
    width: calc(100% / 4.31042);
    margin: 10px;
    background-color: #fff;
    box-shadow: 0px 0px 15px rgba(#000, 0.2);

    @media (max-width: 1024px) {
      width: calc(100% / 3.3);
      justify-content: center;
      align-items: center;
    }
    @media (max-width: 991px) {
      width: calc(100% / 2.2);
    }
    @media (max-width: 480px) {
      width: 100%;
      margin: 10px 0;
    }

    .image-container {
      height: 250px;
      overflow: hidden;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: center;
      // justify-content: center;
      text-align: center;
      img {
        padding-top: 26px;
        height: 80%;
        display: block;
      }
      .options {
        position: absolute;
        display: flex;
        justify-content: space-between;
        width: 100%;
        .favorite {
          margin-left: 7px;
          body.ar & {
            margin-right: 7px;
            margin-left: unset;
          }

          margin-top: 7px;
          width: 30px;
          height: 30px;
          background-color: $color-main;
          border-radius: 100px;
          display: flex;
          justify-content: center;
          align-items: center;
          transition: 0.5s;
          i {
            color: #fff;
            font-size: 14px;
          }
          &:hover {
            background-color: $color-dark;
          }
        }
        .sale {
          width: 60px;
          height: 30px;
          background-color: #fef200;
          display: flex;
          justify-content: center;
          color: #000;
          align-items: center;
        }
      }
    }
    .content {
      text-align: center;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      h2 {
        font-size: 20px;
        font-family: "Roboto-Regular";
      }
      .stars-box {
        i {
          font-size: 14px;
          color: #919296;
          &.gold {
            color: #fef200;
          }
          &:not(:last-of-type) {
            margin-right: 10px;
            body.ar & {
              margin-right: unset;
              margin-left: 10px;
            }
          }
        }
      }
      .price-box {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding-top: 10px;
        padding-bottom: 10px;
        h3 {
          font-size: 20px;
          margin: 0;
          &.price-new {
            color: #44318d;
          }
          &.price-old {
            margin: 0 10px;
            font-family: "Roboto-Regular";
            color: #a4b3b6;
            text-decoration: line-through;
          }
        }
      }
      a {
        display: flex;
        text-align: center;
        margin: 10px 0;
        color: #fff;
        border-radius: 100px;
        display: flex;
        padding: 7px 10px;
        width: 65%;
        align-items: center;
        justify-content: center;
        text-transform: uppercase;
        font-family: "Roboto-Regular";
        background-image: linear-gradient(
          0deg,
          $color-main,
          $color-main,
          #e91515,
          #e91515
        );
        background-size: 100% 300%;
        transition: all 0.5s ease-in-out;
        i {
          fill: #fff;
          transition: 0.5s;
          font-size: 20px;
          margin-left: 7px;
          body.ar & {
            margin-left: unset;
            margin-right: 7px;
          }
        }
        &:hover {
          background-position-y: 100%;
          color: #fff;
        }
      }
    }
    .cart {
      margin-top: 10px;
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      display: flex;
      color: #000;
      transition: 0.5s;
      span {
        display: block;
        font-family: "Roboto-Regular";
        font-size: 20px;
        transition: 0.5s;
      }
      svg {
        fill: #000;
        width: 20px;
        margin-left: 7px;
        body.ar & {
          margin-left: unset;
          margin-right: 7px;
        }

        transition: 0.5s;
      }
      background-image: linear-gradient(
        0deg,
        $color-dark,
        $color-dark,
        #f7f7f7,
        #f7f7f7
      );
      background-size: 100% 300%;
      transition: all 0.5s ease-in-out;

      &:hover {
        background-position-y: 100%;
        span {
          color: #fff;
        }
        svg {
          fill: #fff;
        }
      }
    }
  }
}
