.min-button {
  border: none;
  outline: none;
  color: #fff;
  border-radius: 100px;
  display: flex;
  padding: 5px 15px;
  width: 140px;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  font-family: "Roboto-Regular";
  background-image: linear-gradient(
    0deg,
    $color-dark2,
    $color-dark2,
    $color-main,
    $color-main
  );
  background-size: 100% 300%;
  transition: all 0.5s ease-in-out;
  svg {
    fill: #fff;
    transition: 0.5s;
    width: 17px;
    margin-left: 7px;
    body.ar & {
      margin-left: unset;
      margin-right: 7px;
    }
  }
  &:hover {
    background-position-y: 100%;
    color: #fff;
  }
}
